@import url(https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;1,100&display=swap);
*{
  font-family: 'Roboto', sans-serif;
}
html{
  scroll-behavior: smooth;
 
}
body {
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  transition: 0.5s ease all;
}
.link-list {
  position: fixed;
  z-index: 100;
  width: 100%;
  display: flex;
  list-style: none;
  flex-direction: row;
  padding: 20px;
  justify-content: center;
  align-items: center;
}
.navbar > ul > li {
  margin-left: 20px;
}
.link-list > li > a {
  text-decoration: none;
  color: white;
  font-weight: 600;
  font-size: 14px;
}
.row_banner {
  background: url(/portfolio/static/media/header-background.3b9d0a7b.jpeg);
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  position: relative;
}
.banner-text {
  color: white;

}
.banner-text > h1 {
  font-size: 60px;
}
.banner-text > p {
  text-align: center;
  font-size: 12px;
}
.connect {
  display: flex;
  justify-content: center;
  margin-top: 30px;
}
.connect > a {
  color: white;
  margin-left: 20px;
}
.nav-about {
  position: absolute;
  top: 90%;
}
.arrow {
  border: solid white;
  border-width: 0 3px 3px 0;
  display: inline-block;
  padding: 3px;
}
.down {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
.dark {
  background-color: black;
}

@media only screen and (min-width: 375px) and (max-width: 812px) {
 
  .banner-text > h1 {
    text-align: center;
    font-size: 30px;
  }
  .connect_me{
    font-size: 10px;
  }
  .creator{
    color: white;
    font-weight: 800;
  }
}

.about_container{
   
    background-color:#0064B4 ;
    margin: 100px 30px;
    padding: 80px;
    padding-left: 20px;
    display: flex;
    flex-direction: row;
    
}

#dp{
    width: 200px;
    height: 200px;
    border-radius: 50%;
    border: 5px solid blanchedalmond;
}

.main-content{
    width: 100%;
    font-size: 15px;
    line-height: 20px;
    margin-left: 30px;
    color:white;
    display: flex;
    flex-direction: column;
   
 
}
.main-content>section>h3{
    font-size: 25px;
    color:white;
    margin-bottom: 20px;
}

.resume_section{
    margin-top: 20px;
    width: 100%;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    display: flex;
    line-height: 25px;
}
#resume_download{
    background-color:white;
    cursor: pointer;
    color: black;
    padding: 10px 20px;
    border: none;
    border: 2px solid white;
    margin-right: 20px;
    text-decoration: none;
    transition: 0.5s ease all;

}
#resume_download:hover{
    border: 2px solid white;
    background-color:#0064B4;
    color: white;
}
@media only screen and (min-width: 375px) and (max-width: 812px) {

    .about_container{
        display: flex;
        width: 90%;
        flex-direction: column;
        margin: 10px auto;
    }
    .main-content{
        margin-top: 20px;
    }
    .resume_section{
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }
    #resume_download{
        margin-top: 30px;
    }
    .resume_section>div>h3{
        margin-bottom: 10px;
    }
    .my_info{
       display: flex;
       flex-direction: column;
       align-items: flex-start;
    }
}
.resume_container {
  margin: 10px 30px;
  display: flex;
  padding: 50px;
  flex-direction: column;
}
hr {
  border-bottom: 1px solid #eeeeee;
}
.education,
.internship,
.skills {
  display: flex;
  align-items: center;
  padding: 20px;
  margin-top: 20px;
  margin-right: 30px;
  border-bottom: 1px solid #eeeeee;
}
.edu {
  flex-direction: column;
  margin-bottom: 30px;
}
.edu > div {
  margin-bottom: 15px;
}
.edu > div > em,
.edu > div > p,
.intern > p {
  margin-left: 5px;
}
.education > h2,
.internship > h2,
.skills > h2 {
  flex: 1 1;
}

.ski {
  padding-top: 30px;
}
.ski > p {
  display: flex;
  font-family: "roboto";
  flex-direction: column;
  margin-top: 10px;
}

progress {
  background-color: white;
  width: 300px;
  height: 30px;
  margin-bottom: 30px;
}
@media only screen and (min-width: 375px) and (max-width: 812px) {
  .resume_container {
    margin: 20px;
    padding: 10px;
    padding-left: 0px;
    padding-right: 0px;
  }
  .education,
  .internship,
  .skills {
    padding: 20px;
    display: flex;
    text-align: left;
    flex-direction: column;
    width: 100%;
    align-items: flex-start;
  }
  .education > h2 , .internship>h2 {
    border-bottom: 5px solid #0064b4;
    width: 100%;
    margin-bottom: 30px;
  }
  .ski {
    margin-bottom: 20px;
  }
}

.work_container {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  background-color: #0064b4;
  margin-top: 100px;
  text-align: center;

  height: 400px;
}
.work_container > em {
  color: white;
  margin-bottom: 30px;
}
.project_works {
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.project_works > a {
  width: 300px;
  height: 200px;
}
.project_works > div:hover {
  transform: scale(1.01);
  cursor: pointer;
}
.project_container {
  position: relative;
  margin-left: 10px;
  
}
.project_description {
  display: none;
}
.thumbnail { 
  position: absolute;
  width: 300px;
  height: 300px;
  top: 0;
  left: 0;
}
@media only screen and (min-width: 375px) and (max-width: 812px) {
  .work_container {
    margin-top: 50px;
   
    background-color: white;
  }
  .thumbnail {
    display: none;
  }
  .project_description {
    display: block;
  }
  .work_container > em {
    text-align: left;
    font-size: 20px;
    color: black;
    margin: 0px 20px;
    padding-bottom: 8px;
    border-bottom: 5px solid #0064b4;
  }
  .project_works {
    display: flex;

    flex-direction: column;
  }
  .project_works > a {
    text-decoration: none;
    height: 30px;
    color: black;
    background-color: #eeee;
    align-items: flex-start;
    width: 90%;
    padding: 20px;
    height: 80px;
    margin-left: 20px;
    margin-top: 20px;
    text-align: left;
  }
  .description {
    margin-top: 10px;
    font-size: 15px;
    color: gray;
  }
}

.contact_container{
    height: 600px;
    margin: 20px 100px;
    justify-content: space-around;
    padding-left: 20px;
    padding-right: 20px;
    padding-top: 30px;
    color: black;
}
#form_info{
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    margin-top: 100px;
    height: 300px;
   
}
#form_info>div{
    
    display: flex;
    padding: 10px;
    justify-content: space-between;
}
input,textarea{
    width: 400px;
    padding:10px 20px;
    height: 50px;
    border: none;
    background-color: lightgray;
    outline: none;
}
textarea{
    resize: vertical;
    max-height: 150px;
}
.connect_me{
  
   display: flex;
   align-items: center;
   justify-content: center;
   height: 200px;
   background-color: #0064B4;
   color: grey;
}
.connect_me>a{
    color: white;
    margin-left: 25px;
}
.footer_container{
    display: flex;
    flex-direction: column;
    position: relative;
}
.footer_container>em{
    position: absolute;
    color: white;
    top: 80%;
    width: 100%;
    text-align: center;
}
#submit{
    padding: 10px 30px;
    border: none;
    background-color: #0064B4;
    color:white;
    transition: 0.7s ease all;
}
#submit:hover{
    
    color: white;
   
    opacity: 0.7 ;
    
}
form > div > p{
   
    color: black;
    font-size: 14px;
}
form>div>p>span{
    color: #0064B4;
}
@media only screen and (min-width: 375px) and (max-width: 812px) {
  .contact_container{
      margin: 0px auto;
      margin-top: 20px;
  } 
    .contact_container>h1{
        font-size: 15px;
    
    }
    #form_info{
        margin-top: 20px;
    }
    #form_info>div{
        display: flex;
      
        flex-direction: column;
    }
    #form_info>div>p{
        margin-bottom: 10px;

    }
    input,textarea,button{
        width: 90%;
    }
    .footer_container{margin-top: 20px;}
}
